import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HttpModule } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { FIREBASE_CONFIG } from './app.firebase.config';

import { Facebook } from '@ionic-native/facebook';
import { GooglePlus } from '@ionic-native/google-plus';
import { GlobalProvider } from '../providers/global/global';
import { PacientesProvider } from '../providers/pacientes/pacientes';
import { DoencasCronicasProvider } from '../providers/doencas-cronicas/doencas-cronicas';
import { MedicosProvider } from '../providers/medicos/medicos';
import { ParentesProvider } from './../providers/parentes/parentes';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, { mode: 'md' }, 
{
  links: [
    { loadChildren: '../pages/alergias/alergias.module.ngfactory#AlergiasPageModuleNgFactory', name: 'AlergiasPage', segment: 'alergias', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cadastro/cadastro.module.ngfactory#CadastroPageModuleNgFactory', name: 'CadastroPage', segment: 'cadastro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cirurgias/cirurgias.module.ngfactory#CirurgiasPageModuleNgFactory', name: 'CirurgiasPage', segment: 'cirurgias', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cartao/cartao.module.ngfactory#CartaoPageModuleNgFactory', name: 'CartaoPage', segment: 'cartao', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/doencas-cronicas/doencas-cronicas.module.ngfactory#DoencasCronicasPageModuleNgFactory', name: 'DoencasCronicasPage', segment: 'doencas-cronicas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/historico/historico.module.ngfactory#HistoricoPageModuleNgFactory', name: 'HistoricoPage', segment: 'historico', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#MedicosPageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info-pessoal-medico/info-pessoal-medico.module.ngfactory#InfoPessoalMedicoPageModuleNgFactory', name: 'InfoPessoalMedicoPage', segment: 'info-pessoal-medico', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info-pessoal/info-pessoal.module.ngfactory#InfoPessoalPageModuleNgFactory', name: 'InfoPessoalPage', segment: 'info-pessoal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/med-uso-continuo/med-uso-continuo.module.ngfactory#MedUsoContinuoPageModuleNgFactory', name: 'MedUsoContinuoPage', segment: 'med-uso-continuo', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/medicos/medicos.module.ngfactory#MedicosPageModuleNgFactory', name: 'MedicosPage', segment: 'medicos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/parentes/parentes.module.ngfactory#ParentesPageModuleNgFactory', name: 'ParentesPage', segment: 'parentes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/resetar-senha/resetar-senha.module.ngfactory#ResetarSenhaPageModuleNgFactory', name: 'ResetarSenhaPage', segment: 'resetar-senha', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sobre/sobre.module.ngfactory#SobrePageModuleNgFactory', name: 'SobrePage', segment: 'sobre', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpModule,
    IonicStorageModule.forRoot(),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireDatabaseModule,
    AngularFireAuthModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    Facebook,
    GooglePlus,
    GlobalProvider,
    PacientesProvider,
    DoencasCronicasProvider,
    MedicosProvider,
    ParentesProvider
  ]
})
export class AppModule { }



